<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <!--  -->
        <el-row>
          <el-col>
            <el-button type="primary" @click="showAddCatDialog"
              ></el-button
            >
          </el-col>
        </el-row>
        
        <!--  -->
        <tree-table
          :data="cateList"
          :columns="columns"
          :selection-type="false"
          :expand-type="false"
          show-index
          index-text="#"
          border
          :show-row-hover="false"
        >
          <!--  -->
          <template slot="isOk" slot-scope="scope">
            <i
              class="el-icon-success"
              v-if="scope.row.cat_deleted === false"
              style="color: lightgreen"
            ></i>
            <i class="el-icon-error" v-else style="color: red"></i>
          </template>
          <!--  -->
          <template slot="order" slot-scope="scope">
            <el-tag v-if="scope.row.cat_level === 0"></el-tag>
            <el-tag v-else-if="scope.row.cat_level === 1" type="success"
              >
            </el-tag>
            <el-tag v-else type="warning"></el-tag>
          </template>
          <!--  -->
          <template slot="option" slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.cat_id)"
              >
            </el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeCate(scope.row.cat_id)"
              ></el-button
            >
          </template>
        </tree-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-size="queryInfo.pagesize"
          :page-sizes="[5, 7, 10, 15]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addCatDialogVisible"
        width="50%"
        @close="addCateDialogClosed"
      >
        <el-form
          :model="addCatForm"
          :rules="addCatrules"
          ref="addCatRef"
          label-width="100px"
        >
          <el-form-item label="">
            <!-- options  -->
            <!-- props  -->
            <el-cascader
              v-model="selectedOptions"
              :options="parentCateList"
              @change="CateChange"
              :props="{
                expandTrigger: 'hover',
                checkStrictly: true,
                ...cascaderProps,
              }"
              clearable
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCatForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCatDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editCateDialogVisbel"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form
          :model="editCate"
          :rules="editCateRules"
          ref="editCateRef"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editCate.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editCateDialogVisbel = false"> </el-button>
          <el-button type="primary" @click="editCateInfo"> </el-button>
        </span>
      </el-dialog>
    </div>
  </template>
        
  <script>
  export default {
    name: 'Cate',
    data() {
      return {
        //
        queryInfo: {
          type: 3, //123 
          pagenum: 1, //
          pagesize: 5, //
        },
        total: 0, //
        //
        cateList: [],
        //
        columns: [
          {
            label: '',
            prop: 'cat_name',
          },
          {
            label: '',
            type: 'template', // 
            template: 'isOk', // 
          },
          {
            label: '',
            type: 'template',
            template: 'order',
          },
          {
            label: '',
            type: 'template',
            template: 'option',
          },
        ],
        addCatDialogVisible: false, //
        //
        addCatForm: {
          cat_name: '', //
          cat_pid: 0, // ID
          cat_level: 0, //
        },
        //
        addCatrules: {
          cat_name: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
        parentCateList: [], // 
        //
        cascaderProps: {
          value: 'cat_id',
          label: 'cat_name',
          children: 'children',
        },
        //id
        selectedOptions: [],
        editCateDialogVisbel: false, //
        editCate: {}, //
        editCateRules: {
          cat_name: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
      }
    },
    created() {
      this.getCateList()
    },
    methods: {
      //
      async getCateList() {
        const { data: res } = await this.$http.get('categories', {
          params: this.queryInfo,
        })
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.cateList = res.data.result
        this.total = res.data.total
      },
      //
      showAddCatDialog() {
        this.addCatDialogVisible = true
        this.getParentCateList()
      },
      //pagesize
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getCateList()
      },
      //pagenum
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getCateList()
      },
      //
      async getParentCateList() {
        const { data: res } = await this.$http.get('categories', {
          params: { type: 2 },
        })
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.parentCateList = res.data
      },
      //
      CateChange() {
        if (this.selectedOptions.length > 0) {
          this.addCatForm.cat_pid =
            this.selectedOptions[this.selectedOptions.length - 1]
          this.addCatForm.cat_level = this.selectedOptions.length
          return
        } else {
          this.addCatForm.cat_pid = 0
          this.addCatForm.cat_level = 0
        }
      },
      // 
      async addCate() {
        this.$refs.addCatRef.validate(async (valid) => {
          if (!valid) return
          const { data: res } = await this.$http.post(
            'categories',
            this.addCatForm
          )
          if (res.meta.status !== 201) {
            return this.$message.error(res.meta.msg)
          }
          this.$message.success(res.meta.msg)
          this.getCateList()
          this.addCatDialogVisible = false
        })
      },
      //
      addCateDialogClosed() {
        this.$refs.addCatRef.resetFields()
        this.selectedOptions = []
        this.addCatForm.cat_pid = 0
        this.addCatForm.cat_level = 0
      },
      //
      async showEditDialog(id) {
        const { data: res } = await this.$http.get('categories/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.editCate = res.data
        this.editCateDialogVisbel = true
      },
      //
      editCateInfo() {
        this.$refs.editCateRef.validate(async (valid) => {
          if (!valid) {
            return this.$message.error('')
          } else {
            const { data: res } = await this.$http.put(
              'categories/' + this.editCate.cat_id,
              { cat_name: this.editCate.cat_name }
            )
            if (res.meta.status !== 200) {
              return this.$message.error(res.meta.msg)
            } else {
              this.$message.success(res.meta.msg)
              this.editCateDialogVisbel = false
              this.getCateList()
            }
          }
        })
      },
      //
      editDialogClosed() {
        this.editCateId = 0
        this.editCate = {}
      },
      //
      async removeCate(id) {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        })
          .then(async () => {
            const { data: res } = await this.$http.delete('categories/' + id)
  
            if (res.meta.status !== 200) {
              return this.$message.error(res.meta.msg)
            } else {
              this.$message.success(res.meta.msg)
              this.getCateList()
            }
          })
          .catch(() => {
            this.$message.info('')
          })
      },
    },
  }
  </script>
        
  <style lang="css" scoped>
  .zk-table {
    margin-top: 15px;
  }
  .el-cascader {
    width: 100%;
  }
  
  </style>