<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="102"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="80"></el-table-column>
        <el-table-column prop="goods_number" label="" width="80"></el-table-column>
        <el-table-column prop="add_time" label="" width="160">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <!--  -->
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.goods_id)">
              </el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeGoodById(scope.row.goods_id)">
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum" :page-sizes="[10, 15, 20, 30]" :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisbel" width="50%" @close="editDialogClosed">
      <el-form :model="editGood" :rules="editGoodRules" ref="editGoodRef" label-width="100px">
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editGood.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="editGood.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="editGood.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="editGood.goods_number" type="number"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisbel = false"> </el-button>
        <el-button type="primary" @click="editGoodInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
      
<script>
export default {
  name: 'GoodsList',
  data() {
    return {
      // 
      queryInfo: {
        query: '', //
        pagenum: 1, //
        pagesize: 10, //
      },
      goodslist: [], //
      total: 0, //
      editDialogVisbel: false,
      editGoodDialogVisbel: false, //
      editGood: {}, //
      editGoodRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^\d+(\.(?!.*0$)\d{1,2})?$/,
            message: ',',
            trigger: 'blur',
          },
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^\d+(\.(?!.*0$)\d{1,2})?$/,
            message: '',
            trigger: 'blur',
          },
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[1-9]\d*$/,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    //
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        this.$message.error(res.meta.msg)
      } else {
        this.goodslist = res.data.goods
        this.total = res.data.total
      }
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    //
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editGood = res.data
      this.editDialogVisbel = true
    },
    //
    editDialogClosed() {
      this.editGood = {}
    },
    //
    editGoodInfo() {
      this.$refs.editGoodRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        } else {
          const { data: res } = await this.$http.put(
            `goods/ ${this.editGood.goods_id}`,
            {
              goods_name: this.editGood.goods_name,
              goods_price: this.editGood.goods_price,
              goods_number: this.editGood.goods_number,
              goods_weight: this.editGood.goods_weight,
              goods_cat: this.editGood.goods_cat,
            }
          )
          if (res.meta.status !== 200) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.editDialogVisbel = false
            this.getGoodsList()
          }
        }
      })
    },
    //
    async removeGoodById(id) {
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(async () => {
          const { data: res } = await this.$http.delete('goods/' + id)
          if (res.meta.status !== 200) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.getGoodsList()
          }
        })
        .catch(() => {
          this.$message.info('')
        })
    },
    goAddPage() {
      this.$router.push('/goods/add')
    },
  },
}
</script>
      
<style  scoped>
</style>