<template>
    <el-container class="home_container">
      <!--  -->
      <el-header class="img_text_box ">
        <div>
          <img src="../assets/logo.png" alt="" />
          <span></span>
        </div>
        <el-button type="info" @click="logout"></el-button>
      </el-header>
      <el-container>
        <!--  -->
        <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class="toggle-button" @click="toggleCollapse">
            <!-- {{ isCollapse ? '' : '' }} -->
            |||
          </div>
          <!--  -->
          <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF" :unique-opened="true"
            :collapse-transition="false" :collapse="isCollapse" :router="true" :default-active="activePath">
            <!--  -->
            <el-submenu :index="item.id + ''" v-for="item in menuList" :key="item.id">
              <template slot="title">
                <i :class="iconsObj[item.id]"></i>
                <span>{{ item.authName }}</span>
              </template>
              <!--  -->
              <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id"
                @click="saveNacState('/' + subItem.path)">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span>{{ subItem.authName }}</span>
                </template>
                </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!--  -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </template>
        
  <script>
  export default {
    name: 'Home',
    data() {
      return {
        menuList: [],
        iconsObj: {
          125: 'el-icon-user-solid',
          103: 'el-icon-s-help',
          101: 'el-icon-s-shop',
          102: 'el-icon-s-claim',
          145: 'el-icon-s-marketing',
        },
        isCollapse: false,
        activePath: '',
      }
    },
    created() {
      this.getMenuList();
      this.activePath = window.sessionStorage.getItem('activePath')
    },
    methods: {
      //
      toggleCollapse() {
        this.isCollapse = !this.isCollapse
      },
      //
  
      async logout() {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        })
          .then(async () => {
            window.sessionStorage.clear()
            this.$router.push('/login')
            this.$message.success('')
          })
          .catch(() => {
            this.$message.info('')
          })
      },
      //
      async getMenuList() {
        const { data: res } = await this.$http.get('menus')
        if (res.meta.status !== 200) return this.$message.error(res.meta.mag)
        this.menuList = res.data
      },
      //
      saveNacState(activePath) {
        window.sessionStorage.setItem('activePath', activePath)
        this.activePath = activePath
        console.log(this.activePath);
      },
    },
  }
  </script>
  
  <style scoped>
  .right {
    position: absolute;
    top: 10px;
    right: 50px;
    background-color: rgb(69, 69, 69);
  }
  
  .el-container {
    height: 100%;
  }
  
  .el-header {
    background: #333744;
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    align-items: center;
    color: white;
    font-size: 22px;
    font-weight: 600;
    letter-spacing: 0.2em;
    text-shadow: 2px 2px 10px #ccc;
  }
  
  
  .el-aside {
    transition: all 0.8s;
    background: #333744;
  }
  
  /* 1px  */
  .el-aside .el-menu {
    border-right: 0px;
  }
  
  .el-main {
    background-color: #fff;
  }
  
  .img_text_box {
    height: 100%;
  }
  
  .img_text_box img {
    width: 60px;
    height: 90%;
    border-radius: 50%;
    margin: 5px;
    /* fill-opacity: inherit; */
  }
  
  .img_text_box span {
    position: absolute;
    top: 5px;
    margin-left: 15px;
  }
  
  b {
    position: absolute;
    top: 35px;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0.5em;
    margin-left: 15px;
  }
  
  .toggle-button {
    background-color: #545b66;
    font-size: 10px;
    line-height: 30px;
    color: #fff;
    text-align: center;
    letter-spacing: 0.3em;
    cursor: pointer;
  }
  
  .toggle-button:hover {
    background-color: #474d56
  }
  </style>
  