<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <el-row>
          <el-col :span="8">
            <el-input placeholder="" clearable>
              <el-button slot="append" icon="el-icon-search"> </el-button>
            </el-input>
          </el-col>
        </el-row>
        <!--  -->
        <el-table border stripe :data="orderlist">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="order_number" label=""></el-table-column>
          <el-table-column prop="order_price" label=""></el-table-column>
          <el-table-column prop="pay_status" label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.pay_status === '1'" type="success"
                ></el-tag
              >
              <el-tag v-else type="danger"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.is_send === ''" type="success"
                ></el-tag
              >
              <el-tag v-else type="danger"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="">
            <template slot-scope="scope">
              {{ scope.row.create_time | dateFormat }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  @click="showBox(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
                :enterable="false"
              >
                <el-button
                  type="success"
                  size="mini"
                  icon="el-icon-location"
                  @click="showProgress"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
        >
        </el-pagination>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="100px"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              :props="{
                expandTrigger: 'hover',
              }"
              clearable
              :options="cityData"
              v-model="editForm.address1"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="editForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editVisible = false"> </el-button>
          <el-button type="primary" @click="editAddress"
            > </el-button
          >
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="progressVisible" width="50%">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.ftime"
          >
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="progressVisible = false"> </el-button>
          <el-button type="primary" @click="progressVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>
    </div>
  </template>
        
  <script>
  import cityData from './citydata'
  import progress from './kuaidi'
  export default {
    name: 'Order',
    data() {
      return {
        //
        queryInfo: {
          query: ' ', //
          pagenum: 1, //
          pagesize: 5, //
        },
        total: 0, //
        orderlist: [], //
        editVisible: false, //
        //
        editForm: {
          address1: [],
          address2: '',
        },
        //
        editFormRules: {
          address1: [
            { required: true, message: '/', trigger: 'blur' },
          ],
          address2: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
        cityData, //
        progressVisible: false, //
        progressInfo: [], //
      }
    },
    created() {
      this.getOrderList()
    },
    methods: {
      // 
      async getOrderList() {
        const { data: res } = await this.$http.get('orders', {
          params: this.queryInfo,
        })
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        } else {
          this.orderlist = res.data.goods
          this.total = res.data.total
        }
      },
      // 
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getOrderList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getOrderList()
      },
      //
      showBox(row) {
          console.log(row);
        this.editVisible = true
      },
      // 
      editDialogClosed() {
        this.$refs.editFormRef.resetFields()
      },
      //
      editAddress(){
          this.$refs.editFormRef.validate(async (valid) => {
          if (!valid) {
            return this.$message.error('')
          } 
          this.$message.success("")
          this.editVisible=false
        })
  
      },
      //
      showProgress() {
        const res = progress[0]
        this.progressInfo = res.data
        if (res.meta.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success(res.meta.message)
          this.progressVisible = true
        }
      },
    },
  }
  </script>
        
  <style  scoped>
  .el-cascader {
    width: 100%;
  }
  </style>