<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <el-alert
          title=""
          type="info"
          center
          show-icon
          :closable="false"
        >
        </el-alert>
        <!--  -->
        <el-steps
          :space="200"
          :active="activeIndex - 0"
          finish-status="success"
          align-center
        >
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          label-position="top"
        >
          <!-- tab -->
          <el-tabs
            :tab-position="'left'"
            v-model="activeIndex"
            :before-leave="beforeTabLeave"
            @tab-click="tabClicked"
          >
            <el-tab-pane label="" name="0">
              <el-form-item label="" prop="goods_name">
                <el-input v-model="addForm.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_price">
                <el-input v-model="addForm.addprice"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_weight">
                <el-input v-model="addForm.addweight"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_number">
                <el-input v-model="addForm.addnumber" type="number"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_cat">
                <el-cascader
                  :options="catelist"
                  :props="{
                    expandTrigger: 'hover',
                    ...cateProps,
                  }"
                  v-model="addForm.goods_cat"
                  @change="handleChange"
                >
                </el-cascader>
              </el-form-item>
            
            </el-tab-pane>
            <el-tab-pane label="" name="1">
              <el-form-item
                v-for="item in manyTableData"
                :label="item.attr_name"
                :key="item.attr_id"
              >
                <el-checkbox-group v-model="item.attr_vals">
                  <el-checkbox
                    border
                    :label="cb"
                    v-for="(cb, i) in item.attr_vals"
                    :key="i"
                  ></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
             
            </el-tab-pane>
            <el-tab-pane label="" name="2">
              <el-form-item
                :label="item.attr_name"
                v-for="item in onlyTableData"
                :key="item.attr_id"
              >
                <el-input v-model="item.attr_vals"></el-input>
              </el-form-item>          
            </el-tab-pane>
            <el-tab-pane label="" name="3">
              <el-upload
                action="http://127.0.0.1:8888/api/private/v1/upload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                list-type="picture"
                :headers="headerObj"
                :on-success="handleSuccess"
              >
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">
                  jpg/png500kb
                </div>
              </el-upload>
            </el-tab-pane>
            <el-tab-pane label="" name="4">
              <quill-editor v-model="addForm.goods_introduce"> </quill-editor>
              <el-button type="primary" class="addBtn" @click="addGood">
                </el-button
              >
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </el-card>
      <!-- // -->
      <el-dialog title=" " :visible.sync="previewVisible" width="50%">
        <img :src="previewPath" alt="" class="previewImg" />
      </el-dialog>
    </div>
  </template>
        
  <script>
  import _ from 'lodash'
  export default {
    name: 'Add',
    data() {
      return {
        //
        activeIndex: '0',
        //
        addForm: {
          goods_name: '',
          goods_price:0,
          goods_weight:0,
          goods_number:0,
          // 
          goods_cat: ['11'],
          pics: [], //
          goods_introduce: '', //
          attrs: [], //
        },
        //
        addFormRules: {
          goods_name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          goods_price: [
            { required: true, message: '', trigger: 'blur' },
            {
              pattern: /^\d+(\.(?!.*0$)\d{1,2})?$/,
              message: ',',
              trigger: 'blur',
            },
          ],
          goods_weight: [
            { required: true, message: '', trigger: 'blur' },
            {
              pattern: /^\d+(\.(?!.*0$)\d{1,2})?$/,
              message: '',
              trigger: 'blur',
            },
          ],
          goods_number: [
            { required: true, message: '', trigger: 'blur' },
            {
              pattern: /^[1-9]\d*$/,
              message: '',
              trigger: 'blur',
            },
          ],
          goods_cat: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
        // 
        catelist: [],
        cateProps: {
          label: 'cat_name',
          value: 'cat_id',
          children: 'children',
        },
        // 
        manyTableData: [],
        // 
        onlyTableData: [],
        //
        headerObj: {
          Authorization: window.sessionStorage.getItem('token'),
        },
        previewPath: '', //
        previewVisible: false, //
      }
    },
    created() {
      this.getCateList()
    },
    methods: {
      // 
      async getCateList() {
        const { data: res } = await this.$http.get('categories')
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        } else {
          this.catelist = res.data
        }
      },
      //
      handleChange() {
        if (this.addForm.goods_cat.length !== 3) {
          this.addForm.goods_cat = []
        }
      },
      //tab
      beforeTabLeave(activeName, oldActiveName) {
        if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
          this.$message.error('')
          return false
        }
      },
      //tab
      async tabClicked() {
        //tab
        if (this.activeIndex === '1') {
          const { data: res } = await this.$http.get(
            `categories/${this.cateId}/attributes`,
            {
              params: { sel: 'many' },
            }
          )
          if (res.meta.status !== 200) {
            return this.$message.error(res.meta.msg)
          } else {
            res.data.forEach((item) => {
              item.attr_vals =
                item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
            })
            this.manyTableData = res.data
          }
        }
        //tab
        else if (this.activeIndex === '2') {
          const { data: res } = await this.$http.get(
            `categories/${this.cateId}/attributes`,
            { params: { sel: 'only' } }
          )
          if (res.meta.status !== 200) {
            this.$message.error(res.meta.msg)
          } else {
            this.onlyTableData = res.data
          }
        }
      },
      //
      handlePreview(file) {
        this.previewPath = file.response.data.url
        this.previewVisible = true
      },
      //
      handleRemove(file) {
        //
        const filePath = file.response.data.tmp_path
        //addform
        const i = this.addForm.pics.findIndex((x) => {
          x.pic = filePath
        })
        //
        this.addForm.pics.splice(i, 1)
        console.log(this.addForm)
      },
      //
      handleSuccess(res) {
        if (res.meta.status !== 200) {
          this.$message.error(res.meta.msg)
        } else {
          //addform
          const picInfo = { pic: res.data.tmp_path }
          this.addForm.pics.push(picInfo)
          this.$message.success(res.meta.msg)
        }
      },
      //
      addGood() {
        this.$refs.addFormRef.validate(async (valid) => {
          if (!valid) {
            return this.$message.error('')
          }
          const form = _.cloneDeep(this.addForm)
          form.goods_cat = form.goods_cat.join(',')
          // 
          this.manyTableData.forEach((item) => {
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals.join(' '),
            }
            this.addForm.attrs.push(newInfo)
          })
          // 
          this.onlyTableData.forEach((item) => {
            const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
            this.addForm.attrs.push(newInfo)
          })
          form.attrs = this.addForm.attrs
          const { data: res } = await this.$http.post('goods', form)
  
          if (res.meta.status !== 201) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.$router.push('/goods')
          }
        })
      },
    },
    computed: {
      cateId() {
        if (this.addForm.goods_cat.length === 3) {
          return this.addForm.goods_cat[2]
        }
        return null
      },
      addprice(){
        return this.addForm.goods_price===0?' ':this.addForm.goods_price
      },
      addweight(){
       return this.addForm.goods_weight===0?' ':this.addForm.goods_weight
      },
       addnumber(){
       return this.addForm.goods_number===0?' ':this.addForm.goods_number
      }
    },
  }
  </script>
        
  <style  scoped>
  .el-input {
    width: 85%;
  }
  .el-form,
  .el-steps {
    margin-left: 100px;
  }
  .el-checkbox {
    margin: 0 10px 0 0 !important;
  }
  .previewImg {
    width: 80%;
  }
  .addBtn {
    margin-top: 15px;
    float: right;
    margin-right: 15%;
  }
  
  </style>