<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="10"
          ><el-input
            placeholder=""
            v-model.trim="queryInfo.query"
            clearable
            @clear="getUserList"
            ><el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userList" stripe border>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChange(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column label="" width="174">
          <!--  -->
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 3, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addForm"
        :rules="ruleFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="ruleFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" 
    :visible.sync="setRoleDialogVisible" width="50%"
    @close="setRoleDialogClosed">
      <div>
        <p>{{ userInfo.username }}</p>
        <p>{{ userInfo.role_name }}</p>
        <p>
          
          <el-select v-model="selectRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
      
<script>
export default {
  name: 'Users',
  data() {
    return {
      //
      queryInfo: {
        query: '', //
        pagenum: 1, //
        pagesize: 9, //
      },
      userList: [], //
      total: 0, //
      addDialogVisible: false, //
      //
      addForm: {},
      //
      ruleFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur',
          },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[0-9a-zA-Z]{6,20}$/,
            message: ' 6  20 ',
            trigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/,
            message: '',
            trigger: 'blur',
          },
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      editDialogVisible: false, //
      //
      editForm: {},
      setRoleDialogVisible: false, //
      userInfo: {}, //
      rolesList: [], //
      selectRoleId: {}, //id
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    //
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.userList = res.data.users
      this.total = res.data.total
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getUserList()
    },
    //
    async userStateChange(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        this.$message.error(res.meta.msg)
        userinfo.mg_state = !userinfo.mg_state
      } else {
        this.$message.success(res.meta.msg)
      }
    },
    //
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    //
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        } else {
          //
          const { data: res } = await this.$http.post('users', this.addForm)
          if (res.meta.status !== 201) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.addDialogVisible = false
            this.getUserList()
          }
        }
      })
    },
    //
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        } else {
          //
          const { data: res } = await this.$http.put(
            'users/' + this.editForm.id,
            { email: this.editForm.email, mobile: this.editForm.mobile }
          )
          if (res.meta.status !== 200) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.editDialogVisible = false
            this.getUserList()
          }
        }
      })
    },
    //
    async removeUserById(id) {
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(async () => {
          const { data: res } = await this.$http.delete('users/' + id)
          if (res.meta.status !== 200) {
            return this.$message.error(res.meta.msg)
          } else {
            this.$message.success(res.meta.msg)
            this.getUserList()
          }
        })
        .catch(() => {
          this.$message.info('')
        })
    },
    //
    async setRole(userInfo) {
      this.userInfo = userInfo
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        this.rolesList = res.data
      }
      this.setRoleDialogVisible = true
    },
    //
    async saveRoleInfo() {
      if (!this.selectRoleId) {
        return this.$message.error('')
      } else {
        const { data: res } = await this.$http.put(
          `users/${this.userInfo.id}/role`,
          { rid: this.selectRoleId }
        )
        if (res.meta.status !== 200) {
          this.$message.error(res.meta.msg)
        } else {
          this.getUserList()
          this.$message.success(res.meta.msg)
          this.setRoleDialogVisible = false
        }
      }
    },
    //
    setRoleDialogClosed(){
      this.selectRoleId='',
      this.userInfo=[]
    }
  },
}
</script>
      
<style scoped>
</style>