<template>
    <div class="login_container">
      <!--  -->
      <div class="login_box">
        <!--  -->
        <div class="avater_box">
          <img src="../assets/logo.png" alt="" />
        </div>
        <!--  -->
  
        <!--  :rules :model -->
        <!-- el-form hide-required-asterisk=true
                  el-form-item required=true -->
        <el-form
          ref="loginFormRef"
          :model="loginForm"
          :rules="loginFormRules"
          :hide-required-asterisk="true"
          label-width="60px"
          class="login_from"
        >
          <!--  -->
          <el-form-item label="" prop="username">
            <el-input
              v-model="loginForm.username"
              placeholder=""
              prefix-icon="el-icon-user-solid dong"
            ></el-input>
          </el-form-item>
  
          <!--  -->
          <!-- prefix-icon="el-icon-user-solid" -->
          <el-form-item label="" prop="password">
            <el-input
              v-model="loginForm.password"
              show-password
              placeholder=""
              prefix-icon="el-icon-key"
            ></el-input>
          </el-form-item>
  
          <!--  -->
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetLoginForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </template>
  
  <script>
  // 
  import '../assets/CSS/CSS.css'
  
  export default {
    name: 'Login',
    data() {
      return {
        loginLoading: false, // 
        loginForm: {
          // 
          username:'admin',
          password:'123456'
        },
        // 
        loginFormRules: {
          // 
          username: [
            { required: true, message: '', trigger: 'blur' },
            {
              min: 3,
              max: 10,
              message: ' 3  10 ',
              trigger: 'blur',
            },
          ],
          // 
          password: [
            { required: true, message: '', trigger: 'blur' },
            {
              min: 6,
              max: 15,
              message: ' 6  15 ',
              trigger: 'blur',
            },
          ],
        },
      }
    },
  
    methods: {
      // 
      resetLoginForm() {
        this.$refs.loginFormRef.resetFields()
      },
      //     validatetrue
       login() {
        this.loginLoading = true
        this.$refs.loginFormRef.validate(async valid => {
          if (!valid) {
            return (this.loginLoading = false)
          }
          const { data: res } = await this.$http.post('login',this.loginForm)
          if (res.meta.status !== 200) {
            this.loginLoading = false
            return this.$message.error(' !')
          }
          this.$message.success('!')
          // 1.  token, sessionStorage(/) localStorage(/)
          //   1.1 API,
          //   1.2 token ,  token  sessionStorage
          window.sessionStorage.setItem('token', res.data.token)
          // 2. , /home
          this.$router.push('/home')
          this.loginLoading = false
        })
       }
    }
  }
  </script>
  
  <style scoped>
  .login_container {
    height: 100%;
    background: #4284db; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      #29eac4,
      #4284db
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to right,
      #29eac4,
      #4284db
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  .login_box {
    /*  */
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    width: 550px;
    height: 400px;
    border-radius: 3px;
    background-color: #fff;
  }
  
  .avater_box {
    position: absolute;
    left: 50%;
    height: 130px;
    width: 130px;
    padding: 10px;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #ccc;
    box-shadow: 0 0 10px #ccc;
    background-color: #fff;
  }
  
  .avater_box img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #ccc;
  }
  
  .login_from {
    position: absolute;
    bottom: 70px;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .btns {
    display: flex;
    margin-right: 50px;
    justify-content: center;
  }
  .btns button {
    width: 130px;
    margin: 0px 20px -30px 30px;
  }
  </style>
  